<template>
  <div class="goods">

    <div class="head">
        <div class="hgoods">
          <router-link to="/Goods/Left" tag="div">商品</router-link>
        </div>
        <div class="hgoods">
          <router-link to="/Goods/right" tag="div">评价</router-link>
        </div>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
  export default {
    name: 'Goods'
  }
</script>

<style lang="scss">
.goods{
    width: 100%;
    height: 100%;
    .head{
      width: 100%;
      height:0.8rem;
      border-bottom:1px solid #F5F5F5;
      display:flex;
      position: fixed;
      z-index:1;
      background:white;
      .hgoods{
        width: 50%;
        height: 100%;
        div{
          width:15%;
          height:0.45rem;
          font-size:0.24rem;
          color:gray;
          text-align: center;
          margin:0.2rem auto;
        }
        .router-link-active{
          border-bottom:0.05rem solid #3190E8;
          color: #3190E8;
        }
      }
    }
}
</style>



